
@import (reference) '../styles/vars';

.level51-autotranslateField {
  margin-top: @space-1;

  .font-icon-translatable {
    color: @color-cta;
  }

  .level51-modal {
    h3 {
      margin-bottom: @space-2;
    }

    .level51-autotranslateField-sourceValue {
      padding: @space-2;
      background: @color-mono-94;
      margin-bottom: @space-3;

      p {
        &:first-child {
          margin-top: 0;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    .level51-pricingHint {
      p {
        &:first-child {
          margin-top: 0;
        }

        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
}
